import React from "react";
import { useSelector } from "react-redux";
import personalInfo from "../data/personalInfo";

const name = {
  es: "Experiencia",
  en: "Experience",
};

const Experience = () => {
  const lang = useSelector((state) => state.language.translation);
  const title = name[lang];

  const experience = personalInfo.experience[lang];

  return (
    <div className="about-info__experience">
      {" "}
      <h3 className="about-info__title">{title}</h3>
      <p
        className="about-info__text"
        dangerouslySetInnerHTML={{ __html: experience }}
      ></p>
    </div>
  );
};

export default Experience;
