import React, { useState } from "react";
import { useSelector } from "react-redux";
import ExternalLink from "../assets/ExternalLink";
import GithubSVG from "../assets/GithubSVG";

const Project = ({ project, aos }) => {
  const [isOpen, setIsOpen] = useState(false);
  const lang = useSelector((state) => state.language.translation);

  const handleInfo = () => {
    if (isOpen) setIsOpen(false);
    else setIsOpen(true);
  };

  const { name, description } = project[lang];
  const { img, technologies } = project;
  const { external, github } = project.link;

  return (
    <article
      className="project"
      data-aos="fade-up"
      data-aos-once="true"
      data-aos-duration="900"
    >
      <a
        className="project__img"
        href={external}
        rel="noreferrer"
        target="_blank"
      >
        <img src={img} alt={name} />
      </a>
      <div className="project__info">
        <div className="project__core">
          <h1 className="project__title">{name}</h1>
          <p
            className={`project__description ${
              isOpen && "project__description--open"
            }`}
          >
            {isOpen ? description : description.slice(0, 110) + "..."}
          </p>

          <span className="project__details" onClick={handleInfo}>
            {lang === "es" ? "Ver mas" : "See more"}
            <span className="material-icons">
              {isOpen ? "expand_less" : "expand_more"}
            </span>
          </span>
        </div>
        <div className="project__technologies">
          <ul className={`project__list project__list--${isOpen && "open"}`}>
            {" "}
            {technologies.map((e, index) => (
              <li
                className={`project__technology ${e.toLowerCase()}`}
                key={index}
              >
                {e.slice(0, 1).toUpperCase() + e.slice(1)}
              </li>
            ))}
          </ul>
        </div>
        <div className="project__links">
          {github && (
            <a
              className="project__link"
              href={github}
              rel="noreferrer"
              target="_blank"
            >
              <GithubSVG modifier="project__github" />
            </a>
          )}
          {external && (
            <a
              className="project__link"
              href={external}
              rel="noreferrer"
              target="_blank"
            >
              <ExternalLink modifier="project__external" />
            </a>
          )}
        </div>
      </div>
    </article>
  );
};

export default Project;
