import React, { useState } from "react";
import Button from "./Button";

const texts = {
  a: "Click me to Get Start.",
  b: "Oops! you missed me, try again.",
  c: "Again!, are you drunk or something..?\nCome on, <span class='remark'>you can do it!</span>.",
  d: "Agai.. Nah, I'm just kidding,\nplease don't get mad at me 😅\n<span class='remark'>Enjoy the experience, try last time.</span>",
  e: "Agai.. Nah, I'm just kidding,\nplease don't get mad at me 😅\n<span class='remark'>Enjoy the experience, try last time.</span>",
};

const Game = ({ gameIsVisible, setGameIsVisible }) => {
  const [step, setStep] = useState(0);
  const steps = ["a", "b", "c", "d"];

  const handleSteps = () => {
    setStep(step + 1);
    console.log(step, steps.length);
    if (step >= steps.length - 1) {
      setGameIsVisible("inactive");
      sessionStorage.setItem("DCPgame", "inactive");
    }
  };

  return (
    <div className={`game ${gameIsVisible} ${steps[step]}`}>
      <div className="game__core">
        <p
          className={`game__text ${steps[step]}`}
          dangerouslySetInnerHTML={{
            __html: texts[steps[step]] || texts[steps[3]],
          }}
        ></p>
        <Button
          className={`btn game__btn`}
          text="Get Start"
          // data-aos="zoom-in"
          // data-aos-duration="500"
          // data-aos-delay="100"
          onClick={handleSteps}
        />
      </div>
    </div>
  );
};

export default Game;
