const helpIntersection2 = (element) => {
  const cb = (entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        entry.target.classList.add("active");
      }
    });
  };

  const observador = new IntersectionObserver(cb, {
    threshold: 0.4,
  });

  document.querySelectorAll(element).forEach((el) => observador.observe(el));
};

export default helpIntersection2;
