import React, { useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import helpIntersection from "../helpers/helpIntersection";

const Section = ({ children, section }) => {
  const lang = useSelector((state) => state.language.translation);
  const spy = useRef();

  const nameE = section.en.name;
  const { name, description } = section[lang];
  const { Icon } = section;

  useEffect(() => {
    helpIntersection(nameE);
  }, [nameE]);

  return (
    <section className={`section section--${nameE}`} id={nameE} ref={spy}>
      <div
        id="section__info"
        className={`section__info--${nameE}`}
        data-aos="fade-right"
        data-aos-once="true"
        data-aos-duration="700"
      >
        <Icon modifier="section__icon" />
        <h2 className={`section__title section__title--${nameE}`}>
          {name.slice(0, 1).toUpperCase() + name.slice(1)}
        </h2>{" "}
        <span className="section__line"></span>
        {description[lang] && (
          <p
            className={`section__description section__description--${nameE}`}
            dangerouslySetInnerHTML={{ _html: description[lang] }}
          ></p>
        )}
      </div>
      {children}
    </section>
  );
};

export default Section;
