import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { switchLang } from "../store/slices/language/languageSlice";
import Flag from "./Flag";

const languages = ["es", "en"];

const Settings = ({ modifier }) => {
  const lang = useSelector((state) => state.language.translation);
  const dispatch = useDispatch();

  return (
    <div className={`settings ${modifier}`}>
      <span className="settings__transparent"></span>
      <div className="settings__language">
        <Flag modifier="settings__flag" flag={lang} />
        <span className="material-icons language-expand">expand_less</span>
        <span className="settings__l-options">
          {languages.map((l) => {
            if (l === lang) return false;
            return (
              <Flag
                modifier="settings__flag"
                flag={l}
                key={Math.floor(Math.random() * 1000000000)}
                fn={(e) => dispatch(switchLang(l))}
              />
            );
          })}
        </span>
      </div>
    </div>
  );
};

export default Settings;
