import React, { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import Button from "./Button";
import ContactSVG from "../assets/ContactSVG";
import helpIntersection from "../helpers/helpIntersection";

const contact = {
  es: {
    subtitle: "¿Ahora qué?",
    title: "Contactame",
    description:
      "Gracias por llegar hasta aquí! Estoy abierto a oportunidades, si estas buscando por nuevos talentos, <span class='remark'>Contactame!</span>",
    button: "Enviar Correo",
    apologize: "Disculpa, no pude contenerme",
  },
  en: {
    subtitle: "Now What?",
    title: "Contact Me",
    description:
      "Thank you very much for coming this far!  My inbox is open for new opportunities, if you're looking for new talents. <span class='remark'>Contact me!</span>",
    button: "Send Email",
    apologize: "Sorry, I couldn't void it",
  },
};

const Contact = () => {
  const [btnFlip, setbtnFlip] = useState(false);
  const lang = useSelector((state) => state.language.translation);

  const section = useRef();

  const { title, subtitle, description, button, apologize } = contact[lang];

  useEffect(() => {
    helpIntersection("contact");
  }, []);

  useEffect(() => {
    if (lang === "es") {
      document.querySelector(".btn--contact").classList.add("es");
      document.querySelector(".btn--contact").classList.remove("en");
    }
    if (lang === "en") {
      document.querySelector(".btn--contact").classList.add("en");
      document.querySelector(".btn--contact").classList.remove("es");
    }
  }, [lang]);

  const handleLink = (e) => {
    if (!btnFlip) {
      e.preventDefault();

      document.querySelector(".btn--contact").classList.add("active");
      document.querySelector(".contact__apologize").classList.add(`active`);
      setbtnFlip(true);
    } else {
      document.querySelector(".btn--contact").classList.remove("active");
      document.querySelector(".contact__apologize").classList.remove("active");
    }
  };

  return (
    <section
      className="section contact"
      id="contact"
      ref={section}
      data-aos="fade-down"
      data-aos-duration="1000"
      data-aos-delay="300"
      data-aos-once="true"
    >
      <h3 className="contact__subtitle">
        {<ContactSVG modifier="section__icon" />}
        {subtitle}
      </h3>
      <h2 className="contact__title">{title}</h2>
      <p
        className="contact__description"
        dangerouslySetInnerHTML={{ __html: description }}
      ></p>
      <a
        href="mailto:dariochic@gmail.com"
        target="_blank"
        rel="noreferrer"
        onClick={handleLink}
      >
        <span className="contact__apologize">{apologize} 😅</span>
        <Button text={button} className="btn btn--contact" />
      </a>
    </section>
  );
};

export default Contact;
