import React from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import CloseSVG from "../assets/CloseSVG";
import personalInfo from "../data/personalInfo";
import Button from "./Button";
import Social from "./Social";

const profile = {
  es: {
    educationText: "Educación",
    languagesText: "Idiomas",
    statusText: "Estado",
    resume: "Resumen",
  },
  en: {
    educationText: "Education",
    languagesText: "Languages",
    statusText: "Status",
    resume: "Resume",
  },
};

const AboutProfile = () => {
  const lang = useSelector((state) => state.language.translation);
  const navigate = useNavigate();

  const { name, education, languages, status, img } = personalInfo;
  const { educationText, languagesText, statusText, resume } = profile[lang];

  return (
    <div className="about-profile">
      <button
        style={{ background: "none", border: "none" }}
        onClick={(e) => navigate("/#home")}
      >
        <CloseSVG modifier="about-profile__close" />
      </button>
      <figure className="about-profile__img">
        <img src={img} alt="Dario Chic" />
        <figcaption>{name}</figcaption>
      </figure>
      <ul className="about__list">
        <li className="about__item">
          <span className="about__subtitle">{educationText}</span>
          <p className="about-profile__info">{education[lang]}</p>
        </li>
        <li className="about__item">
          <span className="about__subtitle">{languagesText}</span>
          <div className="about-profile__languages">
            {languages.map((l, i) => {
              return (
                <figure className="about-profile__language" key={i}>
                  <img className="flag" src={l.flag} alt={l.name[lang]} />
                  <figure className="lang">{l.name[lang]}</figure>
                  <span className="level">{l.level[lang]}</span>
                </figure>
              );
            })}
          </div>
        </li>
        <li className="about__item">
          <span className="about__subtitle">{statusText}</span>
          <p
            className="about-profile__info"
            dangerouslySetInnerHTML={{ __html: status[lang] }}
          ></p>
        </li>
      </ul>
      <a
        id="download-resume"
        href="../assets/dario-chic-resume.pdf"
        value="download"
        download={true}
        style={{ visibility: "hidden" }}
      >
        Download CV
      </a>
      <Button
        className="btn btn--about-profile"
        text={resume}
        onClick={(e) => document.getElementById("download-resume").click()}
      />

      <Social modifier="about-profile__social" />
    </div>
  );
};

export default AboutProfile;
