import React from "react";
import { useSelector } from "react-redux";
import { NavLink } from "react-router-dom";

const nav = {
  es: {
    about: "Sobre mi",
    experience: "Experiencia",
    skills: "Habilidades",
    hobbies: "Hobbies",
  },
  en: {
    about: "About me",
    experience: "Experience",
    skills: "Skills",
    hobbies: "Hobbies",
  },
};

const AboutNav = () => {
  const lang = useSelector((state) => state.language.translation);

  const { about, experience, skills, hobbies } = nav[lang];

  return (
    <nav className="about-nav">
      <NavLink
        className="about-nav__link"
        activeclassname="active"
        to="/about/description"
      >
        {about}
      </NavLink>
      <NavLink
        className="about-nav__link"
        activeclassname="active"
        to="/about/experience"
      >
        {experience}
      </NavLink>
      <NavLink
        className="about-nav__link"
        activeclassname="active"
        to="/about/skills"
      >
        {skills}
      </NavLink>
      <NavLink
        className="about-nav__link"
        activeclassname="active"
        to="/about/hobbies"
      >
        {hobbies}
      </NavLink>
    </nav>
  );
};

export default AboutNav;
