import React, { useState } from "react";
import { useSelector } from "react-redux";
// import { helpHttp } from "../helpers/helpHttp";
import data from "../data/projects-data";
import Message from "./Message";
import Project from "./Project";
import ErrorSVG from "../assets/ErrorSVG";

const error = {
  es: "Oops! \n ha ocurrido un error, intenta recargar la página",
  en: "Oops! \n Something went wrong, try reloading the page.",
};

const Projects = () => {
  const lang = useSelector((state) => state.language.translation);
  const [projects] = useState(data || []);

  return (
    <div className="section--projects__container">
      {projects.length > 0 ? (
        projects.map((el, i) => (
          <Project project={el} key={Math.floor(Math.random() * 100000000)} />
        ))
      ) : (
        <Message
          modifier="message--projects"
          msg={error[lang]}
          icon={<ErrorSVG modifier="message__icon" />}
        />
      )}
    </div>
  );
};

export default Projects;
