import React from "react";
import { useSelector } from "react-redux";
import About from "./About";

const Modal = () => {
  const modal = useSelector((state) => state.modal.isOpen);
  return (
    <div className={`modal ${modal ? "modal--open" : "modal--close"}`}>
      <About />
    </div>
  );
};

export default Modal;
