import React from "react";
import ReactDOM from "react-dom/client";
import "./styles/index.scss";
import App from "./App";
import * as serviceWorkerRegistration from "./serviceWorkerRegistration";
import reportWebVitals from "./reportWebVitals";
import { HashRouter, Route, Routes } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./store/store";
import AOS from "aos";
import "aos/dist/aos.css";
import ModalOpener from "./utilities/ModalOpener";
import ChangeModal from "./utilities/ChangeModal";

AOS.init();

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <HashRouter>
    <Provider store={store}>
      <Routes>
        <Route path="/" element={<App />}>
          <Route path="about" element={<ModalOpener />}>
            <Route
              path="description"
              element={<ChangeModal tab="about me" />}
            ></Route>
            <Route
              path="experience"
              element={<ChangeModal tab="experience" />}
            />
            <Route path="skills" element={<ChangeModal tab="skills" />} />
            <Route path="hobbies" element={<ChangeModal tab="hobbies" />} />
            <Route path="*" element={<ChangeModal tab="none" />} />
          </Route>
        </Route>
        <Route path="*" element={<div>ERRROR</div>} />
      </Routes>
    </Provider>
  </HashRouter>
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.unregister();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
