import React from "react";
import { useSelector } from "react-redux";
import Social from "./Social";

const copyright = {
  es: "Diseñado y Construido por Dario Chic.",
  en: "Designed and Built by Dario Chic.",
};

const Footer = () => {
  const lang = useSelector((state) => state.language.translation);
  const copy = copyright[lang];

  return (
    <footer className="section footer">
      <div className="footer__social">
        <Social modifier="social--footer" />
      </div>
      <p className="footer__copyright">{copy}</p>
    </footer>
  );
};

export default Footer;
