import React, { useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import Description from "./Description";
import Experience from "./Experience";
import Skills from "./Skills";
import Hobbies from "./Hobbies";
import Message from "./Message";
import ErrorSVG from "../assets/ErrorSVG";

const AboutInfo = () => {
  const tab = useSelector((state) => state.about.tab);
  // const lang = useSelector((state) => state.language.translation);
  const infoWindow = useRef();

  useEffect(() => {
    infoWindow.current.scrollTo(0, 0);
  }, [tab]);

  return (
    <div className="about-info" ref={infoWindow}>
      {tab === "about me" && <Description />}
      {tab === "experience" && <Experience />}
      {tab === "skills" && <Skills />}
      {tab === "hobbies" && <Hobbies />}
      {tab === "none" && (
        <Message
          modifier="about-info__message"
          msg="<h3>404</h3><p>Page not Found</p>"
          icon={<ErrorSVG modifier="message__icon" />}
          dataAos="zoom-in"
          dataAosDuration="700"
          dataAosDelay="1000"
        />
      )}
      {/* <span className="about-info__background">{tab.toUpperCase()}</span> */}
    </div>
  );
};

export default AboutInfo;
