import { useSelector } from "react-redux";
import { HashLink } from "react-router-hash-link";
import ContactSVG from "../assets/ContactSVG";
import HomeSVG from "../assets/HomeSVG";
import PlaygroundSVG from "../assets/PlaygroundSVG";
import ProjectsSVG from "../assets/ProjectsSVG";
import SettingsSVG from "../assets/SettingsSVG";
import Settings from "./Settings";

const sections = [
  {
    icon: <HomeSVG modifier="nav__icon" />,
    name: { es: "inicio", en: "home" },
  },
  {
    icon: <ProjectsSVG modifier="nav__icon" />,
    name: { es: "proyectos", en: "projects" },
  },
  {
    icon: <PlaygroundSVG modifier="nav__icon" />,
    name: { es: "playground", en: "playground" },
  },
  {
    icon: <ContactSVG modifier="nav__icon" />,
    name: { es: "contacto", en: "contact" },
  },
];

const Nav = () => {
  const modal = useSelector((state) => state.modal.isOpen);
  const lang = useSelector((state) => state.language.translation);

  return (
    <nav className={`nav ${modal ? "open-modal" : "close-modal"}`}>
      <div className="nav__links">
        {sections.map((s, i) => (
          <HashLink
            className={`nav__link nav__link--${s.name.en}`}
            to={`/#${s.name.en}`}
            // onClick={(e) => handleSection(e, i, `${s.name.en}`)}
            key={s.name.en + i}
          >
            {s.icon}
            <span className="nav__tag" key={i}>
              {s.name[lang].slice(0, 1).toUpperCase() + s.name[lang].slice(1)}
            </span>
          </HashLink>
        ))}

        <a
          href="#/"
          className={`nav__link nav__link--settings`}
          onClick={(e) => e.preventDefault()}
        >
          <SettingsSVG modifier="nav__icon" />
          <Settings modifier="nav__settings" />
        </a>
      </div>
    </nav>
  );
};

export default Nav;
