const helpIntersection = (element) => {
  const cb = (entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        if (document.querySelector(`.nav__link--${element}`)) {
          document
            .querySelector(`.nav__link--${element}`)
            .classList.add("active");
        }
      } else {
        if (document.querySelector(`.nav__link--${element}`)) {
          document
            .querySelector(`.nav__link--${element}`)
            .classList.remove("active");
        }
      }
    });
  };

  const observer = new IntersectionObserver(cb, {
    threshold: 0.3,
  });

  observer.observe(document.getElementById(element));
};

export default helpIntersection;
