import { createSlice } from "@reduxjs/toolkit";

export const languageSlice = createSlice({
  name: "language",
  initialState: {
    translation: localStorage.getItem("languageDCP") || "en",
  },
  reducers: {
    switchLang: (state, lang) => {
      state.translation = lang.payload;
      localStorage.setItem("languageDCP", state.translation);
      document.documentElement.lang = state.translation;
    },
  },
});

export const { switchLang } = languageSlice.actions;

export default languageSlice.reducer;
