import React from "react";
import { useSelector } from "react-redux";
import personalInfo from "../data/personalInfo";

const name = {
  es: "Sobre mí",
  en: "About me",
};

const Description = () => {
  const lang = useSelector((state) => state.language.translation);
  const description = personalInfo.aboutMe[lang];

  const title = name[lang];
  return (
    <div className="about-info__description">
      <h3 className="about-info__title">{title}</h3>
      <p
        className="about-info__text"
        dangerouslySetInnerHTML={{ __html: description }}
      ></p>
    </div>
  );
};

export default Description;
