import React, { useEffect, useState } from "react";
import { Outlet } from "react-router-dom";
import Header from "./components/Header";
import Main from "./components/Main";
import Modal from "./components/Modal";
import Nav from "./components/Nav";
import Game from "./components/Game";

const ss = sessionStorage.getItem("DCPgame") || "active";

function App() {
  const [loading, setLoading] = useState(false);
  const [gameIsVisible, setGameIsVisible] = useState(ss);
  const [main, setMain] = useState(false);

  useEffect(() => {
    console.log(sessionStorage.getItem("DCPgame"));
    setLoading(true);

    setTimeout(() => {
      document.getElementById("loader").classList.add("inactive");
      setTimeout(() => {
        setLoading(false);
      }, 500);
    }, 800);
  }, []);

  useEffect(() => {
    if (gameIsVisible === "inactive")
      setTimeout(() => {
        setMain(true);
      }, 350);
  }, [gameIsVisible, main]);

  return (
    <>
      {loading ? (
        false
      ) : (
        <>
          <Game
            gameIsVisible={gameIsVisible}
            setGameIsVisible={setGameIsVisible}
          />
          {main && (
            <>
              <Nav />
              <Header />
              <Main />
              <Modal />
              <Outlet />
            </>
          )}
        </>
      )}
    </>
  );
}

export default App;
