import React from "react";

const Message = ({
  msg,
  icon,
  modifier,
  dataAos,
  dataAosDuration,
  dataAosDelay,
}) => {
  return (
    <div
      className={`message ${modifier}`}
      data-aos={dataAos}
      data-aos-duration={dataAosDuration}
      data-aos-delay={dataAosDelay}
    >
      {icon}
      <p
        className="message__text"
        dangerouslySetInnerHTML={{ __html: msg }}
      ></p>
    </div>
  );
};

export default Message;
