import React from "react";
import { useSelector } from "react-redux";
import PlaygroundSVG from "../assets/PlaygroundSVG";
import ProjectsSVG from "../assets/ProjectsSVG";
import Message from "./Message";
import Projects from "./Projects";
import Section from "./Section";
import ClockSVG from "../assets/ClockSVG";
import Contact from "./Contact";
import Footer from "./Footer";

const playground = { es: "Proximamente", en: "Coming soon" };

const sections = {
  projects: {
    es: {
      name: "proyectos",
      description: false,
    },
    en: {
      name: "projects",
      description: false,
    },
    Icon: ProjectsSVG,
  },
  playground: {
    es: {
      name: "playground",
      description: false,
    },
    en: {
      name: "playground",
      description: false,
    },
    Icon: PlaygroundSVG,
  },
};

const Main = () => {
  const modal = useSelector((state) => state.modal.isOpen);
  const lang = useSelector((state) => state.language.translation);
  return (
    <main className={`main ${modal ? "open-modal" : "close-modal"}`}>
      <Section section={sections.projects}>
        <Projects />
      </Section>
      <Section section={sections.playground}>
        <Message
          modifier="message--playground"
          icon={<ClockSVG modifier="message__icon" />}
          msg={playground[lang]}
          dataAos="zoom-in"
          dataAosDuration="700"
          dataAosDelay="500"
        />
      </Section>
      <Contact />
      <Footer />
    </main>
  );
};

export default Main;
