import React from "react";
import EnFlag from "../assets/flags/EnFlag";
import EsFlag from "../assets/flags/EsFlag";

const Flag = ({ flag, modifier, fn }) => {
  return (
    <button
      style={{
        display: "flex",
        alignItems: "center",
        width: "auto",
        height: "fit-content",
        border: "none",
        background: "none",
      }}
      onClick={fn}
    >
      {flag === "es" && <EsFlag modifier={modifier} />}
      {flag === "en" && <EnFlag modifier={modifier} />}
    </button>
  );
};

export default Flag;
