import React from "react";
import { useSelector } from "react-redux";
import personalInfo from "../data/personalInfo";
import Hobbie from "./Hobbie";

const hobbies = {
  es: {
    name: "Hobbies",
    text: "Y como no todo es programación en mi vida, bueno casi no todo. Estas son algunas de las actividades que disfruto hacer en mis ratos libres.",
  },
  en: {
    name: "Hobbies",
    text: "Y como no todo es programación en mi vida, bueno casi no todo. Estas son algunas de las actividades que disfruto hacer en mis ratos libres.",
  },
};

const Hobbies = () => {
  const lang = useSelector((state) => state.language.translation);
  console.log(personalInfo);
  const { name, text } = hobbies[lang];

  return (
    <div className="about-info__description">
      <h3 className="about-info__title">{name}</h3>
      <p className="about-info__text">{text}</p>
      <div className="about-info__hobbies">
        {personalInfo.hobbies.map((e, i) => (
          <Hobbie
            key={i}
            Icon={e.icon}
            title={e[lang].title}
            text={e[lang].text}
          />
        ))}
      </div>
    </div>
  );
};

export default Hobbies;
